import React from 'react';
import Style from './TaskPanel.css';
import { marked } from 'marked';
import PanelContainer from 'experimental/panels/PanelContainer.js';

class TaskPanel extends React.Component
{
    getDescription()
    {
        const config = this.props.taskViewConfig || {};
        return (config.description || '').trim();
    }

    getDescriptionHtml()
    {
        const description = this.getDescription();
        if (!description) return '';
        return marked.parse(description);
    }

    /** @override */
    render()
    {
        const html = this.getDescriptionHtml();
        return (
            <PanelContainer id={this.props.id}
                className={this.props.className}
                style={this.props.style}
                title={TaskPanel.TITLE}>
                <div className={Style.task_container}>
                    <div className={Style.task_markdown}
                        dangerouslySetInnerHTML={{ __html: html }} />
                </div>
            </PanelContainer>
        );
    }
}
Object.defineProperty(TaskPanel, 'TITLE', {
    get: function() { return I18N.toString('component.task.title'); }
});

export default TaskPanel;
