import React from 'react';
import Style from './TestListView.css';

import { guid } from 'util/MathHelper.js';
import { downloadText } from 'util/Downloader.js';

import IconButton from 'experimental/components/IconButton.js';
import IconUploadButton from 'experimental/components/IconUploadButton.js';

import PageContentIcon from 'components/iconset/PageContentIcon.js';
import UploadIcon from 'components/iconset/UploadIcon.js';
import DownloadIcon from 'components/iconset/DownloadIcon.js';
import CrossIcon from 'components/iconset/CrossIcon.js';
import AddIcon from 'components/iconset/AddIcon.js';
import RunningManIcon from 'components/iconset/RunningManIcon.js';

import TestItem, {SUCCESS_MODE, FAILURE_MODE, WORKING_MODE, DEFAULT_MODE} from './TestItem.js';

const ACCEPT_FILE_TYPES = ['.txt'];
const TEST_FILENAME = 'test.txt';
// const TEST_REFRESH_TICKS = 30;

class TestListView extends React.Component
{
    constructor(props)
    {
        super(props);

        this._controlTests = [];
        this._customTests = [];
        this._testName = TEST_FILENAME;
        this._lastControlWords = props.controlWords;
        this._lastCustomWords = props.customWords;

        this.onTestNew = this.onTestNew.bind(this);
        this.onTestUpload = this.onTestUpload.bind(this);
        this.onTestDownload = this.onTestDownload.bind(this);
        this.onTestClose = this.onTestClose.bind(this);
        this.onTestAdd = this.onTestAdd.bind(this);
        this.onTestDelete = this.onTestDelete.bind(this);
        this.onTestTest = this.onTestTest.bind(this);
        this.onTestRunAll = this.onTestRunAll.bind(this);
        this.onGraphChange = this.onGraphChange.bind(this);
        this.onTestValueChange = this.onTestValueChange.bind(this);

        this.syncControlTests(props);
        this.syncCustomTests(props);
    }

    /** @override */
    componentDidMount()
    {
        this.props.graphController.getGraphChangeHandler().addListener(this.onGraphChange);
    }

    /** @override */
    componentWillUnmount()
    {
        this.props.graphController.getGraphChangeHandler().removeListener(this.onGraphChange);
    }

    syncControlTests(props)
    {
        const controlWords = (props.controlWords || [])
            .filter((word) => word && typeof word.word === 'string')
            .map((word) =>
            {
                return {
                    word: word.word,
                    shouldAccept: !!word.shouldAccept
                };
            });

        const nextTests = controlWords.map((word, index) =>
        {
            const prev = this._controlTests[index];
            if (prev && prev.word === word.word && prev.shouldAccept === word.shouldAccept)
            {
                return prev;
            }
            return {
                id: guid(),
                word: word.word,
                shouldAccept: word.shouldAccept,
                ref: null
            };
        });

        this._controlTests = nextTests;
        this._lastControlWords = props.controlWords;
    }

    syncCustomTests(props)
    {
        const customWords = (props.customWords || [])
            .filter((word) => typeof word === 'string')
            .map((word) => word.trim())
            .filter((word) => word.length > 0);

        this._customTests = customWords.map((word, index) =>
        {
            const prev = this._customTests[index];
            if (prev)
            {
                prev.defaultValue = word;
                return prev;
            }
            return {
                id: guid(),
                defaultValue: word,
                ref: null
            };
        });

        this._lastCustomWords = props.customWords;
    }

    getCustomWordList()
    {
        const words = [];
        for (const test of this._customTests)
        {
            const value = (test.ref ? test.ref.getValue() : test.defaultValue) || '';
            const trimmed = value.trim();
            if (trimmed.length > 0)
            {
                words.push(trimmed);
            }
        }
        return words;
    }

    emitCustomWordsChange()
    {
        if (this.props.onCustomWordsChange)
        {
            this.props.onCustomWordsChange(this.getCustomWordList());
        }
    }

    onGraphChange(graph)
    {
        const allTests = this._controlTests.concat(this._customTests);
        for(const test of allTests)
        {
            if (test.ref)
            {
                test.ref.resetStatus();
            }
        }
    }

    onTestNew(e)
    {
        if (!this.isEmpty())
        {
            this.onTestClose(e);
        }

        this.onTestAdd(e);
    }

    onTestUpload(fileBlob)
    {
        const reader = new FileReader();
        reader.onload = (event) => 
        {
            try
            {
                this._testName = fileBlob.name;
                this._customTests.length = 0;

                const tests = event.target.result.split('\n');
                for(let test of tests)
                {
                    test = test.trim();
                    if (test.length > 0)
                    {
                        this._customTests.push({
                            id: guid(),
                            defaultValue: test,
                            ref: null
                        });
                    }
                }

                //Make sure an element exists, at least
                if (this._customTests.length <= 0)
                {
                    this.onTestAdd(null);
                }
                else
                {
                    this.emitCustomWordsChange();
                }
            }
            catch(e)
            {
                reader.abort();
            }
        };
        reader.readAsText(fileBlob);
    }

    onTestDownload(e)
    {
        const testStrings = [];
        for(const test of this._customTests)
        {
            const ref = test.ref;
            if (ref)
            {
                testStrings.push(ref.getValue());
            }
        }

        downloadText(this._testName, testStrings.join('\n'));
    }

    onTestClose(e)
    {
        const tester = this.props.tester;
        if (tester.isTesting())
        {
            tester.stopTest();
        }

        this._customTests.length = 0;
        this.emitCustomWordsChange();
    }

    onTestAdd(e)
    {
        this._customTests.push({
            id: guid(),
            defaultValue: '',
            ref: null
        });
        this.emitCustomWordsChange();
    }

    onTestRunAll(e)
    {
        const allTests = this._controlTests.concat(this._customTests).filter((test) => !!test.ref);
        let i = 0;

        const nextTest = () => 
        {
            const test = allTests[i];
            this.onTestTest(null, test.ref, true, () => 
            {
                ++i;
                if (i < allTests.length)
                {
                    nextTest();
                }
            });
        };
        if (allTests.length > 0)
        {
            nextTest();
        }
    }

    onTestDelete(e, item)
    {
    //Already handled in render()
    }

    onTestValueChange()
    {
        this.emitCustomWordsChange();
    }

    onTestTest(e, item, forceImmediate=false, callback=null)
    {
        const immediate = forceImmediate || this.props.immediate;
        const tester = this.props.tester;
        const itemValue = item.getValue();

        item.setState({status: WORKING_MODE});

        const graphController = this.props.graphController;
        const machineController = this.props.machineController;
        tester.startTest(itemValue, graphController, machineController).then((result) => 
        {
            item.setState({status: result ? SUCCESS_MODE : FAILURE_MODE});
        }, (err) => 
        {
            //It didn't work out :(
            item.setState({status: DEFAULT_MODE});
        });

        if (immediate)
        {
            const promise = tester.runTest(graphController, machineController, true);
            if (callback)
            {
                promise.then(callback);
            }
        }
    }

    isEmpty()
    {
        return this._customTests.length <= 0;
    }
  
    /** @override */
    render()
    {
        if (this._lastControlWords !== this.props.controlWords)
        {
            this.syncControlTests(this.props);
        }
        if (this._lastCustomWords !== this.props.customWords)
        {
            this.syncCustomTests(this.props);
        }

        // const immediate = this.props.immediate;
        const tester = this.props.tester;

        const graphController = this.props.graphController;
        // const machineController = this.props.machineController;

        const empty = this.isEmpty();
        const disabled = tester && tester.isTesting();
        const hasGraph = graphController && !graphController.getGraph().isEmpty();
        const acceptTests = this._controlTests.filter((test) => test.shouldAccept);
        const rejectTests = this._controlTests.filter((test) => !test.shouldAccept);
        const hasAccept = acceptTests.length > 0;
        const hasReject = rejectTests.length > 0;
        const hasControl = hasAccept || hasReject;
        const hasAny = hasControl || !empty;

        return (
            <div id={this.props.id}
                className={Style.test_container +
          ' ' + this.props.className}
                style={this.props.style}>
                <div className={Style.test_sections + (disabled ? ' disabled ': '')}>
                    {hasAccept && (
                        <div className={Style.test_section}>
                            <div className={Style.test_section_title}>w  L</div>
                            <div className={Style.test_list_scroll_container}>
                                <div className={Style.test_list}>
                                    {acceptTests.map((e) =>
                                    {
                                        const id = e.id;
                                        const defaultValue = e.word || '';
                                        let testCallback = this.onTestTest;
                                        if (!tester || graphController.getGraph().isEmpty())
                                        {
                                            testCallback = null;
                                        }
                                        return <TestItem key={id} ref={ref=>e.ref=ref}
                                            defaultValue={defaultValue}
                                            readOnly={true}
                                            showDelete={false}
                                            onTest={testCallback} />;
                                    })}
                                </div>
                            </div>
                        </div>
                    )}
                    {hasReject && (
                        <div className={Style.test_section}>
                            <div className={Style.test_section_title}>w  L</div>
                            <div className={Style.test_list_scroll_container}>
                                <div className={Style.test_list}>
                                    {rejectTests.map((e) =>
                                    {
                                        const id = e.id;
                                        const defaultValue = e.word || '';
                                        let testCallback = this.onTestTest;
                                        if (!tester || graphController.getGraph().isEmpty())
                                        {
                                            testCallback = null;
                                        }
                                        return <TestItem key={id} ref={ref=>e.ref=ref}
                                            defaultValue={defaultValue}
                                            readOnly={true}
                                            showDelete={false}
                                            onTest={testCallback} />;
                                    })}
                                </div>
                            </div>
                        </div>
                    )}
                    <div className={Style.test_section}>
                        <div className={Style.test_section_title}>eigene Wrter</div>
                        <IconButton className={Style.test_list_add}
                            title={'Add'} onClick={this.onTestAdd}>
                            <AddIcon/>
                            <span className={Style.test_list_count}>
                                {!empty ? '' + this._customTests.length : ''}
                            </span>
                        </IconButton>
                        {!empty && (
                            <div className={Style.test_list_scroll_container}>
                                <div className={Style.test_list}>
                                    {this._customTests.map((e, i) => 
                                    {
                                        const id = e.id;
                                        const defaultValue = e['defaultValue'] || '';
                                        let testCallback = this.onTestTest;
                                        if (!tester || graphController.getGraph().isEmpty())
                                        {
                                            testCallback = null;
                                        }
                                        return <TestItem key={id} ref={ref=>e.ref=ref}
                                            defaultValue={defaultValue}
                                            showDelete={true}
                                            onTest={testCallback}
                                            onValueChange={this.onTestValueChange}
                                            onDelete={(e, item) =>
                                            {
                                                this._customTests.splice(i, 1);
                                                this.emitCustomWordsChange();
                                                this.onTestDelete(e, item);
                                            }} />;
                                    })}
                                </div>
                            </div>
                        )}
                    </div>
                </div>
                {hasAny && (
                    <IconButton className={Style.test_list_runall}
                        title={'Run All'}
                        disabled={disabled || !hasGraph}
                        onClick={this.onTestRunAll}>
                        <RunningManIcon/>
                    </IconButton>
                )}
            </div>
        );
    }
}

export default TestListView;
